<template>
  <div>
    <div style="font-size: 16px; position: absolute; top: 12px; right: 40px">
      <el-link
        :underline="false"
        type="primary"
        style="font-size: 16px; cursor: pointer; margin-top: 10px"
        @click="add"
        ><i class="iconfont iconxinjian" style="padding-right: 5px"></i
        >新增</el-link
      >
    </div>

    <div id="top">
      <div>
        <span style="margin-right: 20px">轮播时长(秒)</span>
        <el-input-number
          v-model="interval"
          :min="1"
          :max="60"
          label="描述文字"
          size="mini"
        ></el-input-number>
      </div>
    </div>

    <!-- <div style="margin-bottom:10px;">
        <span
          >已选择<span style="color: rgb(0, 161, 145); padding: 0 5px">{{
            tableSelection.length
          }}</span
          >项</span
        >
        <el-link
          class="table_link"
          type="danger"
          v-if="tableSelection.length"
          :underline="false"
          @click="handleMultiDelete"
          style="margin: -5px 10px 0px 10px"
          >批量删除</el-link
        >
      </div> -->
    <el-table
      v-loading="loading"
      ref="multipleTable"
      :data="tableData"
      size="small"
      border
      :height="height - 130"
      style="width: 100%; text-align: center; line-height: 18px"
    >
      <!-- <el-table-column
          type="selection"
          width="55"
          align="center"
        ></el-table-column> -->
      <el-table-column
        label="图片"
        align="center"
        prop="preview"
        show-overflow-tooltip
      ></el-table-column>
      <el-table-column label="排序" align="center" prop="index">
      </el-table-column>

      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="{ row }">
          <el-switch
            v-model="row.check"
            active-color="#13ce66"
            inactive-color="#ccc"
            style="margin-right: 10px"
          >
          </el-switch>
          <i
            class="el-icon-delete"
            style="margin-right: 0"
            @click="handleDelete(row)"
          ></i>
        </template>
      </el-table-column>
      <template slot="empty">
        <div>
          <i
            class="iconfont iconzanwuziyuan"
            style="font-size: 40px; color: #8c8c8c"
          >
            <p style="font-size: 14px; margin-top: -30px">暂无资源</p>
          </i>
        </div>
      </template>
    </el-table>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[20, 100, 150, 200]"
      :page-size="pageSize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
      style="float: right; margin-top: 20px"
    >
    </el-pagination>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogFormVisible"
      width="650px"
      :close-on-click-modal="false"
      :append-to-body="true"
      @closed="resetForm"
    >
      <el-form :model="form" :rules="rules" ref="ruleForm" size="mini">
        <el-form-item label="图片:">
          <el-upload
            class="avatar-uploader"
            action="https://jsonplaceholder.typicode.com/posts/"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload"
            :before-remove="handleRemove"
          >
            <img v-if="imageUrl" :src="imageUrl" class="avatar" />

            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-form-item>

        <el-form-item label="排序:" prop="password">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelForm" size="mini">取 消</el-button>
        <el-button type="primary" @click="submitForm('ruleForm')" size="mini"
          >提 交</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "banner_cop",
  components: {},
  props: {
    height: {
      type: Number,
    },
  },
  data() {
    return {
      interval: 5,
      loading: false,
      currentPage: 1, //当前页码
      pageSize: 20,
      total: 0,
      searchName: "",
      searchType: "",
      tableData: [
        {
          preview: "预览",
          index: 1,
          check: true,
        },
      ],
      dialogFormVisible: false,
      dialogTitle: "新增",
      form: {
        name: "",
        sign: "",
        account: "",
        password: "",
      },
      rules: {
        name: [
          { required: true, message: "请输入名称", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        sign: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        account: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
        password: [
          { required: true, message: "请输入", trigger: "blur" },
          {
            min: 1,
            max: 32,
            message: "长度在 1 到 32 个字符",
            trigger: "blur",
          },
        ],
      },
      imageUrl: "",
    };
  },
  created() {},
  mounted() {},
  methods: {
    changeAuthority(data) {
      console.log(data);
    },
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          console.log(this.form);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    cancelForm() {
      this.dialogFormVisible = false;
    },
    resetForm() {
      this.$refs["ruleForm"].resetFields();
      this.form = {
        name: "",
        type: "normal",
        setUpTime: "",
        coordinate: {
          longitude: "",
          latitude: "",
        },
        electronicTag: "",
      };
      this.id = null;
      //this.getTableData();
    },

    add() {
      this.dialogTitle = "新增";
      this.dialogFormVisible = true;
    },
    handleEdit(row) {
      this.dialogTitle = "编辑";
      let data = JSON.parse(JSON.stringify(row));
      this.form = data;
      this.dialogFormVisible = true;
      this.id = data.id;
    },

    handleDelete(row) {
      this.$confirm("确定删除?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
        closeOnClickModal: false,
      }).then(() => {
        console.log(row);
      });
    },

    handleSizeChange(val) {
      this.pageSize = val;
    },
    handleCurrentChange(val) {
      this.currentPage = val;
    },

    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
    },
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error("上传图片只能是 JPG 格式!");
      }
      if (!isLt2M) {
        this.$message.error("上传图片大小不能超过 2MB!");
      }
      console.log(file);
      return isJPG && isLt2M;
    },
    handleRemove() {
      this.imageUrl = "";
    },
  },
};
</script>
<style scoped>
#top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
  margin-top: 10px;
  font-size: 14px;
  color: #595959;
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dotted #8c939d;
  border-radius: 5px;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
  border: 1px dotted #8c939d;
  border-radius: 5px;
}
</style>
